html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
header {
  display: flex;
  place-items: center;
  justify-content: space-around;
  background: url("../pictures/4k picture of vegas.jpg");
  background-repeat: no-repeat;
  height: 500px;
  margin: 0px 5px;
}
.Logo {
  position: absolute;
  color: white;
  font-size: 50px;
  left: 0;
  top: 0;
  margin: 10px 20px;
}
.content {
  display: flex;
  place-items: center;
  justify-content: space-around;
  height: 100%;
  width: 100%;
  font-size: 30px;
  color: white;
  background: rgba(0, 0, 0, 0.466); /* Black background with 0.5 opacity */
}
/* #picture {
  position: fixed;
  right: 0;
  bottom: 0;
  min-width: 100%;
  min-height: 100%;
} */
.NavBar {
  background: rgb(82, 82, 110);
  margin: 0px 5px;
  height: 50px;
  display: flex;
  place-items: center;
  border-bottom: 5px solid rgb(253, 115, 115);
}
#NavCon {
  width: 700px;
  display: flex;
  justify-content: space-evenly;
}
a:-webkit-any-link {
  color: white;
  cursor: pointer;
  text-decoration: none;
  transition: 0.5s;
  font-weight: bold;
}
a:-webkit-any-link:hover {
  color: rgb(0, 183, 255);
}
main {
  background: rgb(57, 255, 163);
  margin: 5px 5px;
}
.Page {
  height: 700px;
  border-bottom: 8px solid red;
}
footer {
  background: rgb(97, 197, 255);
  margin: 5px 5px;
}
